.header-main {
  padding-top: 30px;
  padding-bottom: 10px;
  font-size: 28px;
}

.header-main .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}

.header_main__photo {
  height: 240px;
  width: 180px;
  -webkit-box-shadow: 0px 0px 3px rgba(9, 9, 8, 0.73);
          box-shadow: 0px 0px 3px rgba(9, 9, 8, 0.73);
}

.header-main__info {
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-left: 25px;
}

.header-main__info-name {
  margin: 0;
  margin-top: -10px;
  padding: 0;
  padding-bottom: 14px;
  font-size: 100%;
  line-height: 150%;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}

.header-main__info-text {
  margin: 0;
  padding: 0;
  padding-bottom: 9px;
  font-size: 64.28%;
  line-height: 150%;
  font-family: 'Roboto', sans-serif;
}

.header-main__info-text:last-child {
  padding: 0;
}

.header-main__info-thesis {
  margin: 0;
  padding: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 71.42%;
  line-height: 150%;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}

@media (max-width: 1024px) {
  .header-main {
    font-size: 26px;
  }

  .header-main__info-name {
    padding-bottom: 10px;
  }
}

@media (max-width: 768px) {
  .header-main {
    font-size: 20px;
  }

  .header-main .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .header_main__photo {
    -ms-flex-item-align: center;
        align-self: center;
    margin-bottom: 20px;
  }

  .header-main__info {
    margin: 0;
    margin-top: 20px;
  }

  .header-main__info-name,
  .header-main__info-thesis {
    padding-bottom: 5px;
  }

  .header-main__info-text {
    padding-bottom: 4px;
  }
}



